<script>
export default {
  name: 'GreenHarvestSystem',
  data() {
    return {
      mobileMenuOpen: false,
      imageError: false,
      biodiversityImageError: false,
      scrollHandler: null,
      // 使用正确的图片路径
      greenImage: require('@/assets/green.jpeg'),
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    toggleMobileMenu() {
      this.mobileMenuOpen = !this.mobileMenuOpen;
    },
    closeMobileMenu() {
      this.mobileMenuOpen = false;
    },
    handleImageError() {
      console.log('主背景图片加载失败，使用备选图片');
      this.imageError = true;
    },
    handleBiodiversityImageError(event) {
      console.log('生物多样性图片加载失败，使用备选图片');
      event.target.src = 'https://images.unsplash.com/photo-1542601906990-b4d3fb778b09?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=600&q=80';
    },
    handleCardImageError(event) {
      console.log('卡片图片加载失败，使用备选图片');
      event.target.src = 'https://images.unsplash.com/photo-1416879595882-3373a0480b5b?ixlib=rb-4.0.3&auto=format&fit=crop&w=600&q=80';
    },
    initializeScrollEffects() {
      // 导航栏滚动效果 - 使用可选链操作符
      this.scrollHandler = () => {
        const nav = document.querySelector('nav');
        nav?.classList?.toggle('shadow-lg', window.scrollY > 20);
        nav?.classList?.toggle('bg-white/95', window.scrollY > 20);
        nav?.classList?.toggle('backdrop-blur-sm', window.scrollY > 20);
      };
      
      window.addEventListener('scroll', this.scrollHandler);

      // 平滑滚动
      document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.addEventListener('click', (e) => {
          e.preventDefault();
          
          const targetId = anchor.getAttribute('href');
          if (targetId === '#') return;
          
          const targetElement = document.querySelector(targetId);
          if (targetElement) {
            window.scrollTo({
              top: targetElement.offsetTop - 80,
              behavior: 'smooth'
            });
          }
        });
      });
    }
  },
  mounted() {
    // 页面加载时滚动到顶部
    this.scrollToTop();
    this.initializeScrollEffects();
    // 检查图片是否加载成功
    console.log('绿色图片路径:', this.greenImage);
  },
  beforeUnmount() {
    // 组件销毁前移除事件监听器
    if (this.scrollHandler) {
      window.removeEventListener('scroll', this.scrollHandler);
    }
  }
}
</script>

